.content_container_decade{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  font-size: 15px;
  align-items: end;
}

.page_title_decade { 
  grid-column-start: 1; 
  grid-column-end: 5; 
  grid-row-start: 1; 
  grid-row-end: 2; 
  text-align: center;
  height: 80px;
  background-color: #e5c0A9;
  font-family: 'Fira Code', monospace;
  color: #243F50;
}

h1{
  background-color: #e5c0A9;
  color: #243F50;
}

.album_top{
  grid-column-start: span 4;
}

.album_top > img {
  object-fit: cover;
  width: 100%;
  height: auto;
  align-self: end;
}

.album_mid > img {
  object-fit: cover;
  width: 100%;
  height: auto;
  align-self: end;
}

.album_low > img {
  object-fit: cover;
  width: 100%;
  height: auto;
  align-self: end;
}

.album_mid{
  grid-column-start: span 2;
}

.album_low{
  grid-column-start: span 1;
}

@media only screen and (max-width: 768px) {
  .album_low{
    grid-column-start: span 2;
  }
}
  
  .album-title{
    padding-bottom: 15px;
  }

  .album-title:hover{
    color: #e5c0A9;
    transition: 0.3s;
  }