@import url(https://fonts.googleapis.com/css?family=Fira+Code&display=swap);
@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bowlby+One+SC&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 20px;
  left: 36px;
  top: 10px;
}

.site_title{
  height: 20px;
  padding-right: 10px;
  padding-top: 10px;
}

@media only screen and (max-width: 768px) {

  .bm-burger-button {
    position: fixed;
    width: 36px;
    height: 20px;
    left: 10px;
    top: 65px;
  }

  .site_title{
    width: 100%;
    text-align: center;
  }

  .header-container{
    padding-right: 0;
  }
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: rgba(255, 255, 255, 1);
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #e5c0A9;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background:#243F50;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  background-color: #e5c0A9;
  padding: 1em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #243F50;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
  color: #243F50;
}

/* Styling of overlay */
.bm-overlay {
  background: black;
  height: 100%;
  width: 100%;
}

.menu-item:hover{
  color: #e5c0A9;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

*:focus {
  outline: none;
}
.footer_link{
  color: #BB0000;
}

.footer_container:hover > .footer_link{
  color: white;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.content_container{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  font-size: 15px;
  align-items: end;
}

.page_title { 
  grid-column-start: span 3; 
  text-align: center;
  height: 80px;
  background-color: #e5c0A9;
  font-family: 'Fira Code', monospace;
  color: #243F50;
}

h1{
  background-color: #e5c0A9;
  color: #243F50;
}

@media only screen and (max-width: 768px) {
  .content_container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
    font-size: 15px;
    align-items: end;
  }

  .page_title { 
    grid-column-start: span 2; 
    grid-row-end: 1; 
    text-align: center;
    background-color: #e5c0A9;
    color: #243F50;
  }

}

.album > img {
  object-fit: cover;
  width: 100%;
  height: auto;
  align-self: end;
}
  
  .album-title{
    padding-bottom: 15px;
  }

  .album-title:hover{
    color: #e5c0A9;
    -webkit-transition: 0.3s;
    transition: 0.3s;
  }
.centered{
  text-align: center;
}
.content_container_decade{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  font-size: 15px;
  align-items: end;
}

.page_title_decade { 
  grid-column-start: 1; 
  grid-column-end: 5; 
  grid-row-start: 1; 
  grid-row-end: 2; 
  text-align: center;
  height: 80px;
  background-color: #e5c0A9;
  font-family: 'Fira Code', monospace;
  color: #243F50;
}

h1{
  background-color: #e5c0A9;
  color: #243F50;
}

.album_top{
  grid-column-start: span 4;
}

.album_top > img {
  object-fit: cover;
  width: 100%;
  height: auto;
  align-self: end;
}

.album_mid > img {
  object-fit: cover;
  width: 100%;
  height: auto;
  align-self: end;
}

.album_low > img {
  object-fit: cover;
  width: 100%;
  height: auto;
  align-self: end;
}

.album_mid{
  grid-column-start: span 2;
}

.album_low{
  grid-column-start: span 1;
}

@media only screen and (max-width: 768px) {
  .album_low{
    grid-column-start: span 2;
  }
}
  
  .album-title{
    padding-bottom: 15px;
  }

  .album-title:hover{
    color: #e5c0A9;
    -webkit-transition: 0.3s;
    transition: 0.3s;
  }
a{
  color: #eeeae7;
  text-decoration: none;
}

#twitter:hover{
  background-color:#0084b4;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

#lastfm:hover{
  background-color:#BB0000;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  color: white;
}

#spotify:hover{
  background-color:#1DB954;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

html{
  background-color: #243F50;
}

::-moz-selection {
  background: #7a0612; /* WebKit/Blink Browsers */
}

::selection {
  background: #7a0612; /* WebKit/Blink Browsers */
}
::-moz-selection {
  background: #7a0612; /* Gecko Browsers */
}
.home_container{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
  font-size: 15px;
  height: 100%;
}

.home_title { 
  grid-column-start: 1; 
  grid-column-end: 3; 
  grid-row-start: 1; 
  grid-row-end: 2; 
  text-align: center;
  height: 80px;
  background-color: #e5c0A9;
  font-family: 'Fira Code', monospace;
  color: #243F50;
  margin-bottom: 50px;
}

h1{
  background-color: #e5c0A9;
}

.home_outer_text{
  font-size: 30px;
  height: 150px;
  grid-column-start: 1; 
  grid-column-end: 3; 
  grid-row-start: 3; 
  grid-row-end: 5; 
  display: block;
  font-family: 'Pacifico', cursive;
}

.home_outer_image{
  font-size: 30px;
  height: 150px;
  grid-column-start: 1; 
  grid-column-end: 3; 
  grid-row-start: 5; 
  grid-row-end: 7; 
}


.home_inner{
  text-align: center;
  left: 50%;
  top: 50%;
}

.logo_image{
  padding-bottom: 20px;
}

@media only screen and (max-width: 768px) {
  .logo_image{
    padding-bottom: 50px;
  }

  .logo_image > img{
    width: 100%;
  }
}
.show_songs_container{
  font-family: monospace;
}
.year_container{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
  font-size: 15px;
  height: 300px;
}

.year_title { 
  grid-column-start: 1; 
  grid-column-end: 3; 
  grid-row-start: 1; 
  grid-row-end: 2; 
  text-align: center;
  height: 80px;
  background-color: #e5c0A9;
  font-family: 'Fira Code', monospace;
  color: #243F50;
  margin-bottom: 50px;
}

h1{
  background-color: #e5c0A9;
}

.list_link_outer{
  font-size: 50px;
  height: 150px;
}
.list_link_inner{
  border: 2px solid white;
  text-align: center;
  left: 50%;
  top: 50%;
}

.collage_title{
  width: 100%;
  text-align: center;
  color: #243F50;
}

.collage_image > img{
  object-fit: cover;
  width: 100%;
  height: auto;
  align-self: end;
  z-index: 0;
}


